.Person {
  border: 1px solid #999;
  margin-bottom: 10px;
}
.ClosePerson {
  float: right;
  font-weight: 300;
  border: 1px solid #999;
  color: #999;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  margin: 10px 10px 0 0;
}
.ClosePerson:hover {
  background-color: #444;
  color: #fff;
  cursor: pointer;
}



